export const validateItem = values => {
    const errors = {};

    if (!values.name) {
        errors.name = "Ingrese un nombre para el contribuyente.";
    }
    if (!values.rif) {
        errors.rif = "Ingrese un RIF para el contribuyente.";
    }
    if (!values.address) {
        errors.address = "Ingrese una direccin para el contribuyente.";
    }
    if (values.phone) {
        if (values.phone.length < 13) {
            errors.phone = 'El nmero de telfono es muy corto.'
        }
    }

    return errors;
};
