import * as React from 'react'
import BaseForm from '../../components/BaseForm'
import InputContainer from '../../components/InputContainer'
import PasswordInput from '../../components/PasswordInput'
import axios from '../../api'
import { useSnackbar } from 'notistack';

const validateItem = (values) => {
    const errors = {}

    if (!values.current_password) {
        errors.current_password = "Ingrese su contrasea actual.";
    }
    if (!values.new_password) {
        errors.new_password = "Ingrese una nueva contrasea.";
    }
    if (!values.new_password_confirmation) {
        errors.new_password_confirmation = "Ingrese una nueva contrasea.";
    }
    if (values.current_password === values.new_password) {
        errors.new_password = "La nueva contrasea no debe ser igual a la anterior."
    }
    if (values.new_password !== values.new_password_confirmation) {
        errors.new_password_confirmation = "Las contraseas no coinciden.";
    }

    return errors;
}

const Security = () => {
    const { enqueueSnackbar } = useSnackbar();

    const save = React.useCallback(async (values) => {
        try {
            const { data } = await axios.post('/security', values)

            if (data) {
                enqueueSnackbar(
                    `Ha actualizado su contrasea!"`, 
                    { variant: 'success' }
                );
            }
        } catch (error) {
            if (error.response.data.errors) {
                return error.response.data.errors;
            }
        }
    }, [])

    return (
        <BaseForm
            save={save}
            validate={validateItem}
            title='Cambiar contrasea'
            unresponsive
        >
            <InputContainer label='Contrasea actual' md='8'>
                <PasswordInput
                    name="current_password"
                    placeholder="Contrasea actual"
                    fullWidth
                />
            </InputContainer>
            <InputContainer label='Nueva contrasea' md='8'>
                <PasswordInput
                    name="new_password"
                    placeholder="Nueva contrasea"
                    fullWidth
                />
            </InputContainer>
            <InputContainer label='Repita la nueva contrasea' md='8'>
                <PasswordInput
                    name="new_password_confirmation"
                    placeholder="Repita la nueva contrasea"
                    fullWidth
                />
            </InputContainer>
        </BaseForm>
    )
}

export default Security
